.profile {
    color: #E1E3E6;
    margin-top: 66px;
    border: 0.5px solid #424242;
    border-radius: 20px;

    &__content {
        background-color: #222222;
        width: 100%;
        height: 100px;
        margin-top: -25px;
        border-radius: 20px;

        &-info {
            position: relative;
            top: 20px;
            margin-left: 20%;

            .name-info {
                margin-top: 30px;
                color: #E1E3E6;
                font-size: 20px;
                font-weight: 600;
                letter-spacing: 1px;
            }

            .other-info {
                display: flex;
                margin-right: 10px;
                margin-left: -7px;


                .location, .education, .moreDetailed {
                    margin-top: 10px;
                    margin-right: 10px;
                    display: flex;
                    align-items: center;
                }

                .education
                    svg {
                        margin-right: 5px;
                    }
                }
                .moreDetailed {
                    cursor: pointer;
                    svg {
                        margin-right: 3px;
                    }
                }

            }
        }
    }
