.projects {
    color: #E1E3E6;
    background-color: #222222;
    margin-top: 16px;
    border: 0.5px solid #424242;
    border-radius: 20px;
    height: 525px;
    margin-right: 16px;

    span {
        font-size: 18px;
        position: relative;
        top: 10px;
        margin: 20px 0 0 20px;
        font-weight: bold;
    }



    .projects__item {
        margin: 20px;

        .projects__box {

            height: 140px;
            width: 100%;
            border-radius: 10px;
            overflow: hidden;
            background: rgb(185, 150, 150);
            transition: all 0.8s;
            cursor: pointer;
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;

            .overlay {
                padding: 10px;
            }

            &-0, &-1, &-2 {
                .overlay {
                    height: 340px;
                    width: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    background: rgba(71, 70, 70, 0.5);
                    color: white;
                    opacity: 0;
                    transition: all 0.5s;
                }
            
                &:hover {
                    /* transform: translateY(20px); */
                    /* transition: all 0.5s ease; */
            
                    .overlay {
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        opacity: 1;
                    }
                }
            }

            &-0 {
                background:  url('../assets/pokemon.jpg') center center;
                background-size: 388px;                   
            }

            &-1 {
                background:  url('../assets/marvel.jpg') center center;
                background-size: cover;
            }

            &-2 {
                background: url('../assets/sushi.jpg') center center;
                background-size: cover;
            }
        }

        &-title p {
            font-size: 25px;
            text-shadow:  1px 0 1px #000,0 1px 1px #000,-1px 0 1px #000,0 -1px 1px #000;;
        }
    
        &-descr p {
            text-shadow:  1px 0 1px #000,0 1px 1px #000,-1px 0 1px #000,0 -1px 1px #000;;
        }
    
        a {
            text-decoration: none;
        }
    }

}