.headerNav {
    background-color: #222222;
    width: auto;
    height: 50px;
    display: flex;
    justify-content: center;
    border: 0.5px solid #424242;


    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 120;
    direction: ltr;

    &__text {
        color: #E1E3E6;
        font-size: 20px;
        font-weight: 600;
        letter-spacing: 1px;
    }

    .container {
        width: 1000px;
        display: flex;
      
        justify-content: space-between;
        align-items: center;
    }
    
    &__icons {
        display: flex;
        align-items: center;

        :hover {
            transition: 0.2s;
            color: #fff;
        }

    &-git {
        margin-left: 20px;
        color: #424242;
    }

    &-email{
        color: #424242;
    }

    &-avatar {
        margin-left: 20px;
    }
    }
}
