.experience {
    color: #E1E3E6;
    background-color: #222222;
    margin-top: 16px;
    border: 0.5px solid #424242;
    border-radius: 20px;
    height: auto;

    div:nth-child(2) {
      img::before {
        border-left: 2px gray solid;
        height: 150px;
        left: 30px;
        margin-top: 20px;
      }
    }

  span {
      font-size: 18px;
      position: relative;
      top: 10px;
      margin: 20px 0 0 20px;
      font-weight: bold;
  }

  h3, h4 {
    margin: 0;
  }

  .header-card {
    display: flex;
    align-items: center;
  }
    
  .card {
    position: relative;
    margin: 20px 20px 20px 20px;
    padding: 10px;
    background: #333;
    color: gray;
    border-radius: 8px;

    .card__info {
      display: flex;
      flex-direction: column;
      gap: 10px;
    }
  }
  
  .card__info-header {
    display: flex;
    align-items: center;

    .work-img {
      width: 50px;
    }

    .title {
      color: white;
      position: relative;
      margin-left: 10px;
    }
  }

    .prof {
      margin: 0 0 0 60px;
  
       .profession-js, .profession-qa{
      color: white;
    }
  }

