.main-container {
  max-width: 1000px;
  margin: 0 auto;
}

button {
  width: 200px;
  height: 100px;
  margin: 20%;
}

.grid-group {

    display: grid; 

   /* grid-template-rows: 1fr 1fr 1fr; */
   grid-template-columns: 1fr 1fr 1fr 2fr;
   
   gap: 0px;
   height: 100%;
   
}

.grid-group-2 {
  display: grid; 
  /* grid-template-rows: 1fr 1fr 1fr; */
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
   
  gap: 0px;
  height: 100%;
}

.about-me {

  grid-row-start: 1;
  grid-column-start: 1;

  grid-row-end: 2;
  grid-column-end: 4;
}

.example {
   grid-row-start: 1;
   grid-column-start: 4;

   grid-row-end: 2;
   grid-column-end: 6;
}

.skills { 
   grid-row-start: 2;
   grid-column-start: 1;

   grid-row-end: 3;
   grid-column-end: 6;
}

.projects {
 
  grid-row-start: 3;
  grid-column-start: 1;

  grid-row-end: 6;
  grid-column-end: 3;
}

.experience {
  grid-row-start: 3;
  grid-column-start: 3;

  grid-row-end: 6;
  grid-column-end: 6;
}

