.modal {
    height: 100vh;
    width: 100vh;
   
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    /* transition: 0.5s; */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    &__overlay {
        width: 100%;
        height: 100%;
        position: absolute;
        background-color: rgba(0, 0, 0, 0.4);
        z-index: 0;
    }

}


.modal__active {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    opacity: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: fixed;
    /* transition: 0.5s; */
    /* background-color: rgba(0, 0, 0, 0.4); */
    z-index: 2;
}

.modal__content {
    padding: 20px;
    border-radius: 12px;
    background-color: #333333;
    height: 400px;
    width: 400px;
    transition: 0.5s;
    z-index: 3;

    &-header-title {
        margin-top: 0;
    }
    
    &-title {
        display: flex;
        justify-content: space-between;
   
    }

    svg {
        cursor: pointer;
    }
   
}