.contacts {
    color: #E1E3E6;
    background-color: #222222;
    margin-top: 16px;
    border: 0.5px solid #424242;
    border-radius: 20px;
    height: 200px;

    ul {
        list-style: none;
        padding-left: 0;
    }

    span {
        font-size: 18px;
        position: relative;
        top: 10px;
        margin: 20px 0 0 20px;
        font-weight: bold;
    }

    img {
        width: 30px;
        color: #fff;
    }

    a {
        display: flex;
        text-decoration: none;
        color: #E1E3E6;
        align-items: center;
    }

    div {
        display: flex;
        margin: 10px 0 0 10px;
        flex-direction: column;

        :hover {
            font-weight: bold;
        }
    }
}