.profile {
    &__content {
        &-avatar{
            width: 150px;
            height: 150px;
            border-radius: 50%;
            position: absolute;
            margin-top: -75px;
            margin-left: 30px;

            .avatar {
                width: 150px;
                height: 150px;
                border-radius: 50%;
                border: 5px solid #222222;
            }
        }
    }
}